/* CSS Style for Home Page*/
.home-container {
  display: flex;
  justify-content: center;
  flex-direction: row;
  margin-top: 250px;
}

.home-content {
  position: relative;
  display: flex;
  flex-direction: row;
}

.home-content h2 {
  color: #fff;
  font-size: 10em;
  position: absolute;
  transform: translate(-50%, -50%);
}

.home-content h2:nth-child(1) {
  color: transparent;
  -webkit-text-stroke: 2px #144981;
}

.home-content h2:nth-child(2) {
  color: #144981;
  animation: animate 4s ease-in-out infinite;
}

@keyframes animate {
  0%,
  100% {
    clip-path: polygon(
      0% 45%,
      16% 44%,
      33% 50%,
      54% 60%,
      70% 61%,
      84% 59%,
      100% 52%,
      100% 100%,
      0% 100%
    );
  }

  50% {
    clip-path: polygon(
      0% 60%,
      15% 65%,
      34% 66%,
      51% 62%,
      67% 50%,
      84% 45%,
      100% 46%,
      100% 100%,
      0% 100%
    );
  }
}

