/* App.css */
/* App.css */
.app {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 85vh;
  background-color: white;
  margin: 0;
  position: relative;
}

.app h1 {
  color: #144981;
  text-align: center;
  position: absolute;
  top: 38%;
  left: 50%;
  font-size: 100px;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.container {
  width: 250px;
  height: 250px;
  background-color: white;
  border-radius: 50%;
  position: relative;
  animation: spin 5s linear infinite;
}

.element {
  border-radius: 50%;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: orbit 10s linear infinite;
}

.element1 {
  width: 200px;
  height: 200px;
  color: #144981;
  font-size: 50px;
  animation: orbit1 16s linear infinite; /* Slower orbit */
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes orbit {
  0% {
    transform: translate(-50%, -50%) rotate(0deg) translateX(100px) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg) translateX(100px)
      rotate(360deg);
  }
}

@keyframes orbit1 {
  0% {
    transform: translate(-50%, -50%) rotate(0deg) translateX(30px) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg) translateX(30px)
      rotate(360deg);
  }
}

